import { createRouter, createWebHistory } from 'vue-router';
import Home from '@/views/Home.vue';
import Academic from '@/views/Academic.vue';
import Blog from '@/views/blogs/Blog.vue';
import Illusion from '@/views/illusions/Illusions.vue';

const routes = [
  { path: '/', component: Home },        
  { path: '/academic', component: Academic },
  { path: '/blog', name:'Blog', component: Blog },
  {
    path: '/blog/moist-memory', 
    name: 'MoistMemory',
    component: () => import('@/views/blogs/MoistMemory.vue')
  },
  {path: '/illusion', name:'Illusion', component: Illusion },
    {
    path: '/illusion/light-flash', 
    name: 'LightFlash',
    component: () => import('@/views/illusions/LightFlash.vue')
  },
    {
    path: '/academic/research', 
    name: 'Research',
    component: () => import('@/views/academic/Research.vue')
  },
    {
    path: '/academic/research/mitacs', 
    name: 'Mitacs',
    component: () => import('@/views/academic/Mitacs.vue')
  },
      {
    path: '/academic/study', 
    name: 'Study',
    component: () => import('@/views/academic/Study.vue'),
    children: [
      { path: 'junior-projects', component: () => import('@/views/academic/Junior.vue') },
      { path: 'self-learning', component: () => import('@/views/academic/SelfLearning.vue') }
    ]
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes
});

export default router;